.transactionHistory {
  text-align: center;
  width: 900px;
  margin: 100px auto;
  box-shadow: 0 2px 2px 2px rgba(151, 151, 165, 0.46);
  border-radius: 2px;
}
.transactionHead {
  background-color: #43bdd5;
  height: 50px;
  color: #fff;
  text-transform: uppercase;
}
td {
  padding: 15px;
}
th {
  padding: 20px;
}
.transactionTr:nth-child(odd) {
  background-color: #ecf2f3;
}
